<script>
import headerWorld from "@/components/headerWorld.vue";
import footWorld from "@/components/footWorld";
import { registerApi, sendCodeApi } from "@/common/api/register";
export default {
  components: {
    headerWorld,
    footWorld,
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.user.confirmPassword !== "") {
          this.$refs.register.validateField("confirmPassword");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.user.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (!/^1[0-9]{10}$/.test(this.user.phone)) {
          callback(new Error(""));
          return false;
        } else {
          callback();
        }
      }
    };
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      Countdown: "",
      // 
      getCodeisWaiting: false,
      user: {
        phone: "",
        code: "",
        password: "",
        confirmPassword: "",
      },
      registerRules: {
        password: [{ validator: validatePass, trigger: "blur" }],
        confirmPassword: [{ validator: validatePass2, trigger: "blur" }],
        code: [{ required: true, message: "", trigger: "blur" }],
        phone: [{ validator: validatePhone, trigger: "blur" }],
      },
    };
  },
  methods: {
    getCode() {
      //   
      if (this.getCodeisWaiting) {
        return;
      }
      this.$refs.register.validateField("phone", (errorMsg) => {
        if (!errorMsg) {
          this.Countdown = "...";
          this.getCodeisWaiting = true;
          this.sendCode();
        }
      });

      //   this.sendCode(this.user.phone);
    },
    async sendCode() {
      const res = await sendCodeApi({ phone: this.user.phone });
      if (res) {
        this.setTimer();
      }
    },
    setTimer() {
      let holdTime = 59,
        _this = this;
      _this.Countdown = "(60)";
      _this.Timer = setInterval(() => {
        if (holdTime <= 0) {
          _this.getCodeisWaiting = false;
          _this.Countdown = "";
          clearInterval(_this.Timer);
          return;
        }
        _this.Countdown = "(" + holdTime + ")";
        holdTime--;
      }, 1000);
    },
    handleRegister() {
      this.$refs.register.validate(async (valid) => {
        if (valid) {
          const { confirmPassword, password, ...rest } = this.user;
          if (confirmPassword === password) {
            const res = await registerApi({ password, ...rest });
            if (res) {
              this.$message.success("");
              this.$router.push("/loginIndex");
            }
          }
        }
      });
    },
  },
};
</script>

