
/**
 *  
 *
 * @param {String} dateStr 
 * @returns 
 */
export function getDate(dateStr, formate) {
    if (!dateStr) return ""
    return formatDate(dateStr, formate ? formate : "yyyy-MM-dd HH:mm:ss")
  }
  /**
   * 
   *
   * @param {String} dateStr 
   * @returns 
   */
  export function formatDate(dateStr, format) {
    if (!dateStr) return ""
    var date = new Date(parseInt(dateStr))
    return date.pattern(format)
  }
  /** * Date Date String * (M)(d)12(h)24(H)(m)(s)(E)(q)
    1-2  * (y) 1-4 (S) 1 ( 1-3 ) 
   * eg: 
   * (new Date()).pattern('yyyy-MM-dd hh:mm:ss.S')==> 2006-07-02 08:09:04.423
   * (new Date()).pattern('yyyy-MM-dd E HH:mm:ss') ==> 2009-03-10  20:09:04
   * (new Date()).pattern('yyyy-MM-dd EE hh:mm:ss') ==> 2009-03-10  08:09:04
   * (new Date()).pattern('yyyy-MM-dd EEE hh:mm:ss') ==> 2009-03-10  08:09:04
   * (new Date()).pattern('yyyy-M-d h:m:s.S') ==> 2006-7-2 8:9:4.18
   */
  Date.prototype.pattern = function (fmt) {
    var o = {
      "M+": this.getMonth() + 1, // 
      "d+": this.getDate(), // 
      "h+": this.getHours() % 12 === 0 ? 12 : this.getHours() % 12, // 
      "H+": this.getHours(), // 
      "m+": this.getMinutes(), // 
      "s+": this.getSeconds(), // 
      "q+": Math.floor((this.getMonth() + 3) / 3), // 
      S: this.getMilliseconds() // 
    }
    var week = {
      0: "\u65e5",
      1: "\u4e00",
      2: "\u4e8c",
      3: "\u4e09",
      4: "\u56db",
      5: "\u4e94",
      6: "\u516d"
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (this.getFullYear() + "").substr(4 - RegExp.$1.length)
      )
    }
    if (/(E+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (RegExp.$1.length > 1
          ? RegExp.$1.length > 2
            ? "\u661f\u671f"
            : "\u5468"
          : "") + week[this.getDay() + ""]
      )
    }
    for (var k in o) {
      if (new RegExp("(" + k + ")").test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
        )
      }
    }
    return fmt
  }
  /**
   * @description arraybufferbase64
   * @param {*} buffer
   * @returns
   */
  export function arrayBufferToBase64(buffer) {
    let binary = ""
    let bytes = new Uint8Array(buffer)
    for (let len = bytes.byteLength, i = 0; i < len; i++) {
      binary += String.fromCharCode(bytes[i])
    }
    return window.btoa(binary)
  }
  
  /**
   * @description 
   * @param {Blob} blob 
   * @param {String} fileName 
   */
  export function downloadFile(blob, fileName) {
    if ("download" in document.createElement("a")) {
      const elink = document.createElement("a")
      elink.href = URL.createObjectURL(blob)
      elink.download = fileName
      elink.style.display = "none"
      document.body.appendChild(elink)
      elink.click()
      URL.revokeObjectURL(elink.href)
      document.body.removeChild(elink)
    } else {
      navigator.msSaveBlob(blob, fileName)
    }
  }
  /**
   * @description
   */
  export const getObjType = (obj) => {
    var toString = Object.prototype.toString
    var map = {
      "[object Boolean]": "boolean",
      "[object Number]": "number",
      "[object String]": "string",
      "[object Function]": "function",
      "[object Array]": "array",
      "[object Date]": "date",
      "[object RegExp]": "regExp",
      "[object Undefined]": "undefined",
      "[object Null]": "null",
      "[object Object]": "object"
    }
    if (obj instanceof Element) {
      return "element"
    }
    return map[toString.call(obj)]
  }
  export const deepClone = (data) => {
    var type = getObjType(data)
    var obj
    if (type === "array") {
      obj = []
    } else if (type === "object") {
      obj = {}
    } else {
      // 
      return data
    }
    if (type === "array") {
      for (var i = 0, len = data.length; i < len; i++) {
        obj.push(deepClone(data[i]))
      }
    } else if (type === "object") {
      for (var key in data) {
        obj[key] = deepClone(data[key])
      }
    }
    return obj
  }
  
  export const objToFormData = (obj) => {
    let formData = new FormData()
    Object.keys(obj).forEach((key) => {
      const value = obj[key]
      if (value && Array.isArray(value)) {
        value.forEach((item, index) => {
          if (typeof item === "object") {
            if (item instanceof File) {
              formData.append(`${key}[${index}]`, item)
            } else {
              Object.keys(item).forEach((innerKey) => {
                if (item[innerKey]) {
                  formData.append(`${key}[${index}].${innerKey}`, item[innerKey])
                }
              })
            }
          } else {
            if (item) {
              formData.append(`${key}[${index}]`, item)
            }
          }
        })
      } else {
        if (value) {
          formData.append(key, value)
        }
      }
    })
    return formData
  }
  // 
  export const getFileType = (file) => {
    // 
    const reader = new FileReader()
    reader.readAsArrayBuffer(file.slice(0, 4))
    return new Promise((resolve) => {
      reader.onloadend = () => {
        const buffer = reader.result
        const header = new Uint8Array(buffer)
        let type = "unknown"
  
        // 
        if (header[0] === 0xff && header[1] === 0xd8) {
          type = "image/jpeg"
        } else if (
          header[0] === 0x89 &&
          header[1] === 0x50 &&
          header[2] === 0x4e &&
          header[3] === 0x47
        ) {
          type = "image/png"
        } else if (
          header[0] === 0x47 &&
          header[1] === 0x49 &&
          header[2] === 0x46 &&
          header[3] === 0x38
        ) {
          type = "image/gif"
        } else if (header[0] === 0x42 && header[1] === 0x4d) {
          type = "image/bmp"
  
          // 
        } else if (
          header[0] === 0x25 &&
          header[1] === 0x50 &&
          header[2] === 0x44 &&
          header[3] === 0x46
        ) {
          type = "application/pdf"
        } else if (
          header[0] === 0x50 &&
          header[1] === 0x4b &&
          header[2] === 0x03 &&
          header[3] === 0x04
        ) {
          type = "application/zip"
        } else if (
          header[0] === 0x7b &&
          header[1] === 0x5c &&
          header[2] === 0x72 &&
          header[3] === 0x74
        ) {
          type = "text/rtf"
        } else if (
          header[0] === 0x50 &&
          header[1] === 0x4b &&
          header[2] === 0x53 &&
          header[3] === 0x20
        ) {
          type = "application/vnd.ms-powerpoint"
  
          // 
        } else if (
          header[0] === 0x52 &&
          header[1] === 0x49 &&
          header[2] === 0x46 &&
          header[3] === 0x46
        ) {
          const format = new DataView(buffer.slice(8, 12)).getUint32(0, false)
          if (format === 0x5741) {
            type = "video/x-ms-wmv"
          } else if (format === 0x4156) {
            type = "video/x-msvideo"
          } else if (format === 0x4852) {
            type = "video/mp4"
          }
  
          // 
        } else if (
          // eslint-disable-next-line no-dupe-else-if
          header[0] === 0x47 &&
          header[1] === 0x49 &&
          header[2] === 0x46 &&
          header[3] === 0x38
        ) {
          type = "image/gif"
        } else if (
          header[0] === 0x46 &&
          header[1] === 0x4c &&
          header[2] === 0x56 &&
          header[3] === 0x01
        ) {
          type = "video/x-flv"
        }
  
        resolve(type)
      }
    })
  }
  /**
   * @description ()
   * @param {*} name label
   * @param {*} width 
   * @param {*} item 
   */
  export function renderHeaderWidth(name, width, item) {
    if (width && width !== 0) {
      return width
    }
    let realWidth = 0
    let span = document.createElement("span")
    span.innerText = name
    document.body.appendChild(span)
    if (item.sfzcpx === "Y") {
      realWidth = span.getBoundingClientRect().width + 46
    } else if (item.sfss === "Y") {
      realWidth = span.getBoundingClientRect().width + 40
    } else {
      realWidth = span.getBoundingClientRect().width + 22
    }
    document.body.removeChild(span)
    return realWidth
  }
  /**
   * @description 
   *
   */
  export function showFileType(fileName) {
    return fileName.indexOf(".pdf") != -1 ? "application/pdf" : "image"
  }
  

  export function listToTree(list) {
    let info = list.reduce(
      (map, node) => ((map[node.id] = node), (node.children = undefined), map),
      {}
    );
    return list.filter((node) => {
      if(info[node.parentId]) {
        if(!info[node.parentId].children) {
          info[node.parentId].children = []
        }
        info[node.parentId].children.push(node);
      }
      return info[node.parentId] === undefined;
      // return node.parentId == -1;
    });
  }