<script>
import headerWorld from "@/components/headerWorld.vue";
import footWorld from "@/components/footWorld";
import {
  accountLoginApi,
  getUserInfoApi,
  smsCodeLoginApi,
} from "@/common/api/user";
import { mapMutations } from "vuex";
import { sendCodeApi } from "@/common/api/register";
// import rsa from '@/utils/rsa';
export default {
  components: {
    headerWorld,
    footWorld,
  },
  data() {
    var validatePhone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (!/^1[0-9]{10}$/.test(this.codeLoginData.phone)) {
          callback(new Error(""));
          return false;
        } else {
          callback();
        }
      }
    };
    return {
      selected: "option1",
      dialogVisible1: false,
      dialogVisible2: false,
      getCodeisWaiting: false,
      Countdown: "",
      logintype: 1,
      user: {
        account: "",
        password: "",
      },
      codeLoginData: {
        code: "",
        phone: "",
      },
      userRules: {},
      codeLoginRules: {
        code: [{ required: true, message: "", trigger: "blur" }],
        phone: [{ validator: validatePhone, trigger: "blur" }],
      },
    };
  },
  computed: {
    ...mapMutations({
      SET_ACCOUNT: "SET_ACCOUNT",
    }),
  },
  mounted() {
    const nameParam = this.$route.query.name;
    if (nameParam == "mica") {
      this.logintype = 2;
    }
  },
  methods: {
    selectOption(option) {
      this.selected = option;
    },
    handleSubmit() {
      if (this.selected === "option1") {
        this.userPasswordLogin();
      } else {
        this.userCodeLogin();
      }
    },

    async userPasswordLogin() {
      const { password, account } = this.user;
      const res = await accountLoginApi({
        account,
        password: password,
      });
      if (res.code == 0) {
        this.$router.push("/");
        localStorage.setItem("token", res.result);
        const data = await getUserInfoApi();
        this.$store.commit("SET_ACCOUNT", data.result.account);
      }
    },
    handleRegister() {
      this.$router.push("/regIndex");
    },
    userCodeLogin() {
      this.$refs.codeLogin.validate(async (valid) => {
        if (valid) {
          const res = await smsCodeLoginApi({
            ...this.codeLoginData,
          });
          if (res.code == 0) {
            this.$router.push("/");
            localStorage.setItem("token", res.result);
            const data = await getUserInfoApi();
            this.$store.commit("SET_ACCOUNT", data.result.account);
          }
        }
      });
    },

    getCode() {
      //   
      if (this.getCodeisWaiting) {
        return;
      }
      this.$refs.codeLogin.validateField("phone", (errorMsg) => {
        if (!errorMsg) {
          this.Countdown = "...";
          this.getCodeisWaiting = true;
          this.sendCode();
        }
      });

      //   this.sendCode(this.user.phone);
    },
    async sendCode() {
      const res = await sendCodeApi({ phone: this.codeLoginData.phone });
      if (res) {
        this.setTimer();
      }
    },
    setTimer() {
      let holdTime = 59,
        _this = this;
      _this.Countdown = "(60)";
      _this.Timer = setInterval(() => {
        if (holdTime <= 0) {
          _this.getCodeisWaiting = false;
          _this.Countdown = "";
          clearInterval(_this.Timer);
          return;
        }
        _this.Countdown = "(" + holdTime + ")";
        holdTime--;
      }, 1000);
    },
  },
};
</script>

