<template>
  <div>
    <div class="myCourse">
      <!--  -->
      <el-tabs v-model="activeTab">
        <!--  Tab -->
        <el-tab-pane label="" name="submitted">
          <div class="content-box">
            <div
                v-for="(item, index) in submittedWorkList"
                :key="index"
                class="content-item"
                @click="handleItemClick(item)"
            >
              <div class="content-info">
                <h3 class="name">{{ item.name }}</h3>
                <p class="time">: {{ item.time }}</p>
                <p class="comment">: {{ item.comment }}</p>
              </div>
            </div>
          </div>
        </el-tab-pane>

        <!--  Tab -->
        <el-tab-pane label="" name="unsubmitted">
          <div class="content-box">
            <div
                v-for="(item, index) in unsubmittedWorkList"
                :key="index"
                class="content-item"
                @click="handleItemClick(item)"
            >
              <div class="content-info">
                <h3 class="name">{{ item.name }}</h3>
                <p class="time">: {{ item.time }}</p>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { getSubmitWorkApi, getUnsubmitWorkApi } from "@/common/api/user";

export default {
  data() {
    return {
      activeTab: 'submitted', // 
      submittedWorkList: [],  // 
      unsubmittedWorkList: [],  // 
      loading: false,
    };
  },
  created() {
    this.getSubmittedWork();
    this.getUnsubmittedWork();
  },
  methods: {
    // 
    async getSubmittedWork() {
      this.loading = true;
      try {
        const res = await getSubmitWorkApi();
        if (res && res.result) {
          this.submittedWorkList = res.result;
        }
      } catch (error) {
        console.error(":", error);
      } finally {
        this.loading = false;
      }
    },
    // 
    async getUnsubmittedWork() {
      this.loading = true;
      try {
        const res = await getUnsubmitWorkApi();
        if (res && res.result) {
          this.unsubmittedWorkList = res.result;
        }
      } catch (error) {
        console.error(":", error);
      } finally {
        this.loading = false;
      }
    },
    handleItemClick(item) {
      console.log(item, "item");
    },
  },
};
</script>

