<template>
  <div>
    <el-dialog
      center
      title=""
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      @close="close"
      width="60%"
    >
      <div>
        <h2 style="text-align: center; margin-bottom: 10px"></h2>
        <h4 style="text-align: center; margin-bottom: 10px"></h4>
        <div v-html="info.content"></div>
      </div>
      <div
        style="width: 100%; text-align: center"
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          style="width: 80%"
          :disabled="btnDisabled"
          type="primary"
          @click="() => (nameVisible = true)"
          >{{ btnTitle }}</el-button
        >
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible="nameVisible"
      v-model="addData"
      @close="closeName"
      width="30%"
    >
      <span>
        <el-input
          v-model="addData.name"
          placeholder=""
          size="normal"
          clearable
        ></el-input>
      </span>
      <template #footer>
        <span>
          <el-button type="primary" @click="handleRegisterSubmit"
            ></el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { registerApi } from "@/common/api/user";
import { getArticleDetailApi } from '@/common/api/aricle';
export default {
  data() {
    return {
      dialogVisible: false,
      timer: "",
      nameVisible: false,
      btnDisabled: false,
      info: {},
      addData: {
        name: "",
      },
      btnTitle: "",
    };
  },

  methods: {
    open() {
      this.dialogVisible = true;
      this.btnDisabled = true;
      this.getInfo()
      let time = 9;
      this.timer = setInterval(() => {
        // 
        if (time == 0) {
          // 
          this.btnTitle = ``;
          clearInterval(this.timer);
          this.btnDisabled = false;
        } else {
          this.btnTitle = ` ${time}s `;
          time--;
        }
      }, 1000);
    },
    close() {
      this.dialogVisible = false;
      clearInterval(this.timer);
      this.btnTitle = ``;
    },
    async handleRegisterSubmit() {
      const res = await registerApi({ ...this.addData });
      if (res) {
        this.$message.success("");
        this.closeName()
        this.dialogVisible = false
      }
    },
    async getInfo() {
     const res =  await getArticleDetailApi({id: 6})
     this.info = res.result
    },
    closeName() {
        this.addData = {
            name: ""
        }
        this.nameVisible  = false
    }
  },
};
</script>
<style lang="less" scoped>
p {
  line-height: 30px;
}
</style>
