<!--  -->
<template>
  <div>
    <el-dialog
      :title="dialogTitle"
      :close-on-click-modal="false"
      :visible.sync="addModifyVisible"
      :append-to-body="true"
      width="40%"
      @close="close"
    >
      <el-form
        :model="addData"
        :rules="workRule"
        ref="workRef"
        size="small"
        label-position="left"
        label-width="120px"
      >
        <el-form-item label="" prop="work">
          <el-select
            style="width: 100%"
            v-model="addData.work"
            placeholder=""
            value-key="id"
            clearable
            filterable
          >
            <el-option
              v-for="item in workOptions"
              :key="item.id"
              :label="item.name"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label=":" prop="url">
          <upload-image v-model="addData.url" :limit="5"></upload-image>
        </el-form-item>
        <el-form-item label=":" prop="name">
          <el-input
            type="textarea"
            :rows="5"
            v-model="addData.name"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" plain @click="close"> </el-button>
        <el-button size="small" type="primary" @click="handleSubmit"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import uploadImage from "@/components/uploadImage.vue";
import { getCourseWorkList, submitWork } from "@/common/api/user";
export default {
  name: "addCourseWork",
  components: { uploadImage },
  data() {
    return {
      addModifyVisible: false,
      dialogTitle: "",
      chapterIdOptions: [],
      workOptions: [],
      addData: {
        chapterId: "",
        courseId: "",
        name: "",
        url: "",
        work: "",
      },
      workRule: {
        work: [{ required: true, message: "", trigger: "blur" }],
        url: [{ required: true, message: "", trigger: "blur" }],
        name: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },

  created() {},

  mounted() {},

  methods: {
    edit(id) {
      this.dialogTitle = "";
      this.addData.courseId = id;
      // this.chapterIdOptions = row.adCourseChapters;
      this.addModifyVisible = true;
      this.getWorkList(id);
    },

    close() {
      this.$refs.workRef && this.$refs.workRef.clearValidate();
      this.addData = {
        chapterId: "",
        courseId: "",
        name: "",
        url: "",
        work: "",
      };
      this.addModifyVisible = false;
    },

    handleSubmit() {
      this.$refs.workRef.validate(async (valid) => {
        if (valid) {
          const { work,url, ...rest } = this.addData;
          const res = await submitWork({
            workId: work.id,
            url: url  ? JSON.stringify(url.map(item => item.url)) : "",
            ...rest,
            chapterId: work.chapterId,
          });
          if (res) {
            this.$message.success("");
            this.addModifyVisible = false;
          }
        }
      });
    },

    getChapterList() {},
    async getWorkList(courseId) {
      const res = await getCourseWorkList({ courseId });
      this.workOptions = res.result;
    },
  },
};
</script>
<style lang="scss" scoped></style>
